import { Link } from "react-router-dom";

export default function Header() {
  return (
    <div className="header">
      <Link to="/matrix" className={"link"}>
        <p>  (-1)</p>
      </Link>
      <Link to="/kmeans" className={"link"}>
        <p>K-means  (-2)</p>
      </Link>
      <Link to="/algorithm" className={"link"}>
        <p>  Max-min (-3)</p>
      </Link>
      <Link to="/alg4" className={"link"}>
        <p>  (-4)</p>
      </Link>
      <Link to="/neural" className={"link"}>
        <p>  (-5)</p>
      </Link>
      <Link to="/" className={"link"}>
        <p>  (-7  1)</p>
      </Link>
      <Link to="/second" className={"link"}>
        <p>   (-7  2)</p>
      </Link>
      <Link to="/third" className={"link"}>
        <p>   (-7  3)</p>
      </Link>
      <Link to="/fourth" className={"link"}>
        <p>   (-7  4)</p>
      </Link>
    </div>
  );
}
